.check-box{-webkit-appearance:none;position:relative;width:2rem;height:2rem;cursor:pointer;outline:none !important;border:1px solid #dadce0;border-radius:2px;background:#fff}.check-box::before{color:#fff;font-size:2rem;content:"✓";position:absolute;top:50%;left:50%;overflow:hidden;transform:scale(0) translate(-50%, -50%)}.check-box:hover{border-color:#abaeb5}.check-box:checked{background-color:#abaeb5;border:none}.check-box:checked::before{border-radius:.3rem;transform:scale(1) translate(-50%, -50%)}